.content-page {
    display: flex;
    flex-direction: column; /* Single-column layout */
    align-items: left; /* Center the content */
    background-color: black;
    color: white;
    padding: 20px; /* Ensure consistent padding */
    margin-left: 5%;
    box-sizing: border-box;
    text-align: left;
    width: 100%;
    max-width: 70%;
    > a {
        color:rgb(191, 229, 255);
        padding-bottom: 10px;
        font-weight: bold;
    }
}

div.two-col {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 10%;
    margin-right: 10%;
}
    